<template>
	<view class="page bgwhite">
		<view class="list">
			<u--form ref="uform" :model="form" :rules="rules" label-width="110px">
				<u-form-item :label="$t('产品编号')" style="padding-left: 10px;" prop="itemNo">
					<u--input v-model="form.itemNo" :placeholder="$t('please.input')" border="surround"
						style="height: 20px;margin-right: 10px;"></u--input>
				</u-form-item>
				<u-form-item :label="$t('产品名称')" style="padding-left: 10px;" prop="itemName">
					<u--input v-model="form.itemName" :placeholder="$t('please.input')" border="surround"
						style="height: 20px;margin-right: 10px;"></u--input>
				</u-form-item>
				<u-form-item :label="$t('工作中心')" style="padding-left: 10px;" prop="wsCode">
					<u--input v-model="form.wsCode" :placeholder="$t('please.input')" border="surround"
						style="height: 20px;margin-right: 10px;"></u--input>
				</u-form-item>
				<u-form-item :label="$t('机器编号')" style="padding-left: 10px;" prop="machineNo">
					<u--input v-model="form.machineNo" :placeholder="$t('please.input')" border="surround"
						style="height: 20px;margin-right: 10px;"></u--input>
				</u-form-item>
				<u-form-item :label="$t('开始时间')" style="padding-left: 10px;" prop="startDate">
					<view class="itemView">
						<u--input v-model="form.startDate" :placeholder="$t('please.select')" border="surround"
							suffixIcon="arrow-down" suffixIconStyle="color: #909399" style="height: 20px;margin-right: 0px;"
							@focus="startTimeShow = true"></u--input>
						<u-datetime-picker :show="startTimeShow" v-model="valueDate" mode="datetime" :formatter="formatter"
							@cancel="startTimeShow = false" @confirm="stimeConfirm" @change="stimeChangeHandler"
							:cancel-text="$t('取消')" :confirm-text="$t('确认')"></u-datetime-picker>
					</view>
				</u-form-item>
				<u-form-item :label="$t('结束时间')" style="padding-left: 10px;" prop="endtDate">
					<view class="itemView">
						<u--input v-model="form.endDate" :placeholder="$t('please.select')" border="surround"
							suffixIcon="arrow-down" suffixIconStyle="color: #909399" style="height: 20px;margin-right: 0px;"
							@focus="endTimeShow = true"></u--input>
						<u-datetime-picker :show="endTimeShow" v-model="valueEndDate" mode="datetime" :formatter="formatter"
							@cancel="endTimeShow = false" @confirm="etimeConfirm" @change="etimeChangeHandler"
							:cancel-text="$t('取消')" :confirm-text="$t('确认')"></u-datetime-picker>
					</view>
				</u-form-item>
			</u--form>
		</view>

		<u-button class="confirmBtn" shape="circle" :text="$t('screening')" @click="submit"></u-button>
	</view>
</template>

<script>
import Api from "@/api/daily/index.js"
export default {
	data() {
		return {
			form: {
				mo: null,
				machineNo: null,
				dispatchNo: null,
				wsCode: null,
				bcCode: null,
				bcName: null,
				itemNo: null,
				itemName: null,
				startDate: null,
				endDate: null,
			},
			rules: {},
			classeShow: false,
			classeSelectIndex: 0,
			classesList: [],
			bcList: [],
			valueDate: Number(new Date()),
			valueEndDate: Number(new Date()),
			startTimeShow: false,
			endTimeShow: false,
			startTime: null,
			endTime: null
		}
	},
	onShow() {
		uni.setNavigationBarTitle({ // 修改头部标题
			title: this.$t("day.screening")
		});
	},
	onLoad() {
		uni.$u.http.get(Api.bcList).then(result => {
			console.log('result', result);
			if (result.code === 200) {
				if (result.data != null) {
					this.bcList = result.data
					var codeList = []
					result.data.forEach(item => {
						codeList.push(item.name)
					});
					if (codeList != []) {
						this.classesList.push(codeList)
					}
				}
			}
		}).catch(er => {
			console.log('er', er);
		}).finally(() => {
			this.isGet = false
		})
	},
	methods: {
		classesChangeHandler(e) {
			console.log("选择的下标", e.index)
			this.classeSelectIndex = e.index
		},
		classesConfirm() {
			this.classeShow = false
			this.form.bcCode = this.bcList[this.classeSelectIndex].bcCode
			this.form.bcName = this.bcList[this.classeSelectIndex].name
		},

		submit() {
			this.$refs.uform.validate(valid => {
				if (valid) {
					console.log('验证通过');
				} else {
					console.log('验证失败');
				}
			});

			uni.$emit('returnDailyData', this.form)
			uni.navigateBack({
				delta: 1
			});
		},

		stimeChangeHandler(e) {
			console.log("选择的时间=", e.value)
			this.startTime = e.value
		},
		etimeChangeHandler(e) {
			console.log("选择的时间=", e.value)
			this.endTime = e.value
		},
		stimeConfirm() {
			this.startTimeShow = false
			this.form.startDate = this.timestampToTime(this.startTime ? this.startTime : this.valueDate);
		},
		etimeConfirm() {
			this.endTimeShow = false
			this.form.endDate = this.timestampToTime(this.endTime ? this.endTime : this.valueEndDate);
		},
		//时间戳转时间
		timestampToTime(value) {
			let date = new Date(value);
			let y = date.getFullYear(); // 年
			let MM = date.getMonth() + 1; // 月
			MM = MM < 10 ? ('0' + MM) : MM;
			let d = date.getDate(); // 日
			d = d < 10 ? ('0' + d) : d;
			let h = date.getHours(); // 时
			h = h < 10 ? ('0' + h) : h;
			let m = date.getMinutes(); // 分
			m = m < 10 ? ('0' + m) : m;
			let s = date.getSeconds(); // 秒
			s = s < 10 ? ('0' + s) : s;
			return y + '-' + MM + '-' + d + " " + h + ':' + m + ':' + s;
		},

		formatter(type, value) {
			if (type === 'year') {
				return `${value}年`
			}
			if (type === 'month') {
				return `${value}月`
			}
			if (type === 'day') {
				return `${value}日`
			}
			if (type === 'hour') {
				return `${value}时`
			}
			if (type === 'minute') {
				return `${value}分`
			}
			return value
		},
	}
}
</script>

<style lang="scss" scoped>
.list {
	&-item {
		padding: 10upx 16upx;
		@include after();
		font-size: 28upx;
		border: 0;
		justify-content: center;
		align-items: center;

		.label {
			width: 200upx;
			color: #333;
			font-weight: 500;
		}

		.value {
			color: #666;
		}
	}
}

.itemView {
	width: 100%;
	margin-right: 10px;
}

.confirmBtn {
	width: 80%;
	margin-top: 20px;
	margin-bottom: 20px;
	align-items: center;
	color: white;
	font-size: 20px;
	background-color: $ch-theme;
}
</style>